<template>
    <section class="hero-section d-flex align-items-center justify-content-center text-white text-center">
        <div data-aos="fade-up" data-aos-delay="500" class="overlay container-fluid pt-5 pb-1">
            <h1 class="display-4 fw-bold mt-2 py-2">{{ $t('hero_section.primary_line') }}</h1>
            <p class="lead w-sm-50 mx-auto py-2 fo-secondary">{{ $t('hero_section.secondary_line') }}</p>
            <a class="btn btn-join btn-lg mt-3" href="#investments">{{ $t('hero_section.join_button') }}</a>

            <div id="stats" class="container-sm p-5 mt-5">
                <div class="row g-3">
                    <div class="col-md-3" v-for="(item, index) in stats" :key="index">
                        <div class="card shadow-sm bg-semi-transparent">
                            <div class="card-body">
                                <h2 class="display-6 text-success fw-bold">{{ formatNumber(item.current) }}</h2>
                                <h3 class="card-text text-light">{{ $t('stats_section.' + (item.label).trim().toLowerCase()) }}
                                </h3>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script setup>
import { onMounted } from 'vue';
import gsap from 'gsap';

onMounted(() => {
    gsap.from('.hero-content', { opacity: 0, y: -50, duration: 1 });
});
</script>



<style scoped>
.hero-section {
    /* min-height: 100vh; */
    background-image: url('@/assets/hero.jpg');
    background-size: cover;
    background-position: center;
    /* position: relative; */
}

.overlay {
    /* position: absolute;
    top: 0;
    left: 0;
    width: 100%; */
    min-height: 100%;
    background-color: rgba(0, 60, 0, 0.5);
    padding: 3rem;
    border-radius: 1rem;
    color: white;
}

.stats {
    display: flex;
    justify-content: space-around;
    padding: 3rem 2rem;
    background-color: #f4f4f4;
}

.stat {
    text-align: center;
}

.stat h2 {
    font-size: 2.5rem;
    color: #2f8f2f;
}

.stat p {
    font-size: 1.2rem;
    color: #555;
}
</style>

<script>
import { useI18n } from 'vue-i18n';

export default {
    name: 'HeroSection',
    setup() {
        const { t } = useI18n();
        return { t };
    },
    data() {
        return {
            stats: [
                { number: 25000, label: 'Partners', current: 0 },
                { number: 100000, label: 'Investors', current: 0 },
                { number: 35, label: 'Projects', current: 0 },
                { number: 10000000, label: 'Budget', current: 0 }
            ]
        }
    },
    mounted() {
        this.stats.forEach((stat) => {
            const duration = 1500;
            const startTime = performance.now();
            const animate = (now) => {
                const elapsed = now - startTime;
                const progress = Math.min(elapsed / duration, 1);
                stat.current = Math.floor(progress * stat.number);
                if (progress < 1) requestAnimationFrame(animate);
            };
            requestAnimationFrame(animate);
        });
    },
    methods: {
        formatNumber(value) {
            if (value >= 1_000_000_000) return (value / 1_000_000_000).toFixed(1) + 'B'
            if (value >= 1_000_000) return (value / 1_000_000).toFixed(1) + 'M'
            if (value >= 1_000) return (value / 1_000).toFixed(1) + 'K'
            return value.toLocaleString()
        }
    }
};

</script>